@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

*:focus {
    outline:none
}

li {
    list-style: none
}

i {
    cursor: pointer;
}

a {
    color: #2c3e50;
}

a:hover {
    text-decoration: none;
    color: #2c3e50;
}

.search-input {
    width: 100%;
    padding: 15px;
    border: none;
    -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
    -webkit-appearance: none;
    z-index: 100;
    border-radius: 4px;
}

#movies {
    text-align: left;
}

.result-container {
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
    -webkit-appearance: none;
    max-height: 318px;
    height: 318px;
    overflow: hidden
}

tbody, tr, table, .table {
    border:none !important;
}

button {
    background: none;
    border: none;
    cursor: pointer;
}

.list-button i {
    color: rgba(0, 0, 0, 0.1);
    -webkit-transition: color 0.2s ease;
    transition: color 0.2s ease;
}

.list-button i:hover {
    color: rgba(0, 0, 0, 0.3);
}

.delete-button {
    color: rgba(158, 158, 158, 0.3);
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.delete-button:hover {
    color: rgba(74, 71, 157, 1);
    cursor: pointer;
}

#nprogress .bar {
    background: #4A479D;
    height: 3px;
}

#nprogress .spinner {
    display: none;
}

.bold {
    font-weight: bold
}

.table-responsive {
    margin-top: 30px;
    margin-bottom: 30px;
}

.back-button{
    margin-bottom: 30px
}

.back-button span {
    font-size: 19px;
    font-weight: bolder;
    color: rgba(34, 196, 169, 1);
}

.back-button i {
    margin-top: -6px;
    color: rgba(34, 196, 169, 1);
}

.list-button {
    position: absolute;
    top: 30px;
    right: 30px
}

.list-button:hover {
    color: rgba(74, 71, 157, 1);
}

.list-button i {
    font-size: 40px
}

.popular-head-title {
    font-weight: bold;
    margin-bottom: 30px;
    margin-left: 10px
}

.popular {
    margin-top: 100px
}

.add-button {
    color: rgba(34, 196, 169, 0.3);
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.add-button:hover {
    color: rgba(34, 196, 169, 1);
}

.popular-result {
    -webkit-transition: -webkit-transform 0.2s ease;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
    -webkit-transform: scale(1);
            transform: scale(1)
}

.popular-result:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}

.logo {
    margin-top: 100px
}

.movie-details {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.details-btn {
    background: rgba(34, 196, 169, 1);
    color: white;
    font-weight: bold;
    padding: 10px 30px;
    border-radius: 3px;
    -webkit-transition: background 0.2s ease;
    transition: background 0.2s ease;
}

.details-btn:hover {
    background: rgb(29, 170, 147);
    color: white;
}

.star-icon {
    color: #ffe100;
}

.vote {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 20px
}

.form {
    padding: 30px;
    border-radius: 3px
}

.input {
    border-radius: 0;
    width: 100%;
    padding: 15px;
}

.psw {
    margin-top: 20px
}

.btn-sign {
    background: rgba(74, 71, 157, 1);
    width: 100%;
    padding: 15px;
    margin-top: 30px;
    color: white;
    font-weight: bold;
    border-radius: 3px;
    -webkit-transition: -webkit-box-shadow 0.2s ease-in-out;
    transition: -webkit-box-shadow 0.2s ease-in-out;
    transition: box-shadow 0.2s ease-in-out;
    transition: box-shadow 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
    -webkit-box-shadow: 0 0px 30px rgba(74, 71, 157, 0.2);
            box-shadow: 0 0px 30px rgba(74, 71, 157, 0.2);
}

.btn-sign:hover {
    -webkit-box-shadow: 0 0px 30px rgba(74, 71, 157, 0.6);
            box-shadow: 0 0px 30px rgba(74, 71, 157, 0.6);
}

.title {
    margin-bottom: 50px
}

.link-signup {
    color: rgb(74, 71, 157);
    font-weight: bold;
    margin-top: 30px;
    margin-bottom: 0
}